import React, { useContext } from 'react';
import { Link } from 'react-router-dom';

//  store
import { StoreContext } from '../store/store';

/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

const Seti = () => {

    /*   *   *   *   *   *   *   *   *   *   */

    const { gameSize, gameNeed, gameDepth } = useContext( StoreContext );
    const { setGameSize, setGameNeed, setGameDepth } = useContext( StoreContext );
    const { data, pages } = useContext( StoreContext );

    const changeGameSize = ( event ) =>{
        setGameSize( prev => event.target.value );
        setGameNeed( data.calcNeed( event.target.value ));
        setGameDepth( data.calcDepth( event.target.value ));

    }

    const changeGameNeed = ( event ) =>{
        setGameNeed( prev => event.target.value );
    }

    const changeGameDepth = ( event ) =>{
        setGameDepth( prev => event.target.value );
    }

    /*   *   *   *   *   *   *   *   *   *   */

    const min = data.initialValues.minSize;
    const max = data.initialValues.maxSize;

    /*   *   *   *   *   *   *   *   *   *   */

    const isAllSet = ( gameSize && gameNeed && gameDepth ) ? true : false;

    /*   *   *   *   *   *   *   *   *   *   */

    return(
        <section className="main-seti">  

        <div className="elem">
            <h2 className="title">Wybierz rozmiar gry:</h2>
            <input type="range" min={min} max={max} value={gameSize} onChange={(e) => changeGameSize(e)} />
            <span>{gameSize}</span>
        </div>

        <div className="elem">
            <h2 className="title">Wybierz rząd gry:</h2>
            <input type="range" min={data.calcNeed(gameSize)} max={gameSize} value={gameNeed} onChange={(e) => changeGameNeed(e)} />
            <span>{gameNeed}</span>
        </div>

        <div className="elem">
            <h2 className="title">Wybierz poziom:</h2>
            <input type="range" min={2} max={data.calcDepth(gameSize)} value={gameDepth} onChange={(e) => changeGameDepth(e)} />
            <span>{gameDepth}</span>
        </div>

        <Link exact to={pages.game} className={`btn next ${isAllSet && '__isVisible'}`}>Dalej</Link>

    </section>
)};

/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

export default Seti;