import React, { useContext, useEffect } from 'react';
import { Redirect } from 'react-router-dom';

//  store
import { StoreContext } from '../store/store';

//  component
import Board from '../component/board';
import TicTacToe from '../script/ticTacToe';

/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

const Game = () => {

    /*   *   *   *   *   *   *   *   *   *   */

    const { gameSize, gameNeed, gameDepth } = useContext( StoreContext );
    const { playerAI, playerHU, player } = useContext( StoreContext );
    const { gameOver, gameResult, endGame } = useContext( StoreContext );
    const { pages } = useContext( StoreContext );

    /*   *   *   *   *   *   *   *   *   *   */

    const fields = new Array( +gameSize ).fill( new Array( +gameSize ).fill( null ) );
    const gameID = "boardTicTacToe";

    /*   *   *   *   *   *   *   *   *   *   */

    useEffect(() => {

        const ticTacToe = new TicTacToe( `#${gameID}`, gameSize, gameNeed, player, playerAI, playerHU, gameDepth );
        ticTacToe.setUp( endGame );

    }, []);

    /*   *   *   *   *   *   *   *   *   *   */

    if( !player ) { return <Redirect to={`${pages.home}`}/> }

    /*   *   *   *   *   *   *   *   *   *   */

    return(
    <>
        <Board fields={fields} gameID={gameID} size={gameSize} />
        {gameOver && <div className="game-over">{gameResult}</div>}
    </>
)};

/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

export default Game;