import React, { createContext, useEffect, useState } from 'react';

import * as data from './data'

/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

const StoreContext = createContext(null);

/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

const StoreProvider = ({ children }) => {

    /*   *   *   *   *   *   *   *   *   *   */

    const [ gameResult, setGameResult ] = useState( null );
    const [ gameDepth, setGameDepth ] = useState( data.initialValues.gameDepth );
    const [ gameNeed, setGameNeed ] = useState( data.initialValues.gameNeed );
    const [ gameSize, setGameSize ] = useState( data.initialValues.gameSize );

    const [ gameOver, setGameOver ] = useState( false );
    const [ playerAI, setPlayerAI ] = useState( null );
    const [ playerHU, setPlayerHU ] = useState( null );
    const [ player, setPlayer ] = useState( null );

    const [ pages, setPages ] = useState( data.pages );

    /*   *   *   *   *   *   *   *   *   *   */

    const endGame = ( result ) => {
        setGameResult( result );
        setGameOver( true );
    }

    const reinit = () => {

        setGameResult( null );
        setGameDepth( data.initialValues.gameDepth );
        setGameNeed( data.initialValues.gameNeed );
        setGameSize( data.initialValues.gameSize );

        setGameOver( false );
        setPlayerAI( null );
        setPlayerHU( null );
        setPlayer( null );
    }

    /*   *   *   *   *   *   *   *   *   *   */

    useEffect(() => {

    }, []);
    
    /*   *   *   *   *   *   *   *   *   *   */

    const storeValue = {

        //  game propeties
        gameDepth, setGameDepth,
        gameNeed, setGameNeed,
        gameSize, setGameSize,

        //  game status
        gameResult, setGameResult,
        gameOver, setGameOver,
        playerAI, setPlayerAI,
        playerHU, setPlayerHU,
        player, setPlayer,

        //  game setup
        endGame, reinit,

        //  important data
        pages, setPages,

        //  global data
        data
    };

    /*   *   *   *   *   *   *   *   *   *   */

    return(
        <StoreContext.Provider value={storeValue}>
            {children}
        </StoreContext.Provider>
)}

/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

export default StoreProvider;
export { StoreContext, StoreProvider };