/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

const calcNeed = ( data ) => { return Math.floor( 3*Math.log( data )); }
const calcDepth = ( data ) => { return Math.floor(( 66*Math.log( data )) / ( data*data ) + 1 ); }

