import React, { useContext } from 'react';
import { Link } from 'react-router-dom';

//  store
import { StoreContext } from '../store/store';

/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

const Home = () => {

    /*   *   *   *   *   *   *   *   *   *   */

    const { playerAI, playerHU, player } = useContext( StoreContext );
    const { setPlayerAI, setPlayerHU, setPlayer } = useContext( StoreContext );
    const { pages, data } = useContext( StoreContext );

    const setPlayersMarks = ( mode ) => {
        
        if( mode ) {    //  AI -> X || HU -> O
            setPlayerAI( prev => data.mark.O );
            setPlayerHU( prev => data.mark.X );
        }

        if( !mode ) {   //  AI -> O || HU -> X
            setPlayerAI( prev => data.mark.O );
            setPlayerHU( prev => data.mark.X );
        }
    }

    const setPlayerFirst = ( mode ) => {

        if( mode ) {    //  X -> first
            setPlayer( prev => data.mark.X );
        }

        if( !mode ) {   //  O -> first
            setPlayer( prev => data.mark.O );
        }
    }

    /*   *   *   *   *   *   *   *   *   *   */

    const isAllSet = ( playerAI && playerHU && player ) ? true : false;

    /*   *   *   *   *   *   *   *   *   *   */

    return(
    <section className="main-home">  

        <div className="elem">
            <h2 className="title">Wybierz znak:</h2>
            <div className="choice">
                <button className="btn __chioce __type-a" onClick={() => setPlayersMarks(true)}>X</button>
                <button className="btn __chioce __type-b" onClick={() => setPlayersMarks(false)}>O</button>
            </div>
        </div>

        <div className="elem">
            <h2 className="title">Wybierz, kto zaczyna:</h2>
            <div className="choice">
                <button className="btn __chioce __type-a" onClick={() => setPlayerFirst(true)}>X</button>
                <button className="btn __chioce __type-b" onClick={() => setPlayerFirst(false)}>O</button>
            </div>
        </div>

        <Link exact to={pages.seti} className={`btn next ${isAllSet && '__isVisible'}`}>Dalej</Link>

    </section>
)};

/*   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   *   */

export default Home;